.button-click{
    width: 10rem;
    height: 5rem;
    background-color: rgb(6, 81, 147, 0.9);
    color: white;
    border-radius: 15PX;
    margin: 1rem 1rem;
    cursor: pointer;
    font-size: 1.2rem;
 }

 .button-click:hover {
     background-color: rgb(17, 133, 6);
     transition: 1.2s;

 }

.button-reset {
    width: 10rem;
    height: 5rem;
    background-color: rgb(182, 55, 57, 0.9);
    color: white;
    border-radius: 15PX;
    margin: 1rem 1rem;
    cursor: pointer;
    font-size: 1.2rem;
}

.button-reset:hover {
    background-color: rgb(131, 133, 22);
    transition: 1.2s;

}