* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html, body {
    
    background: linear-gradient(to bottom, black 30%,  white );
    font-size: 18px;
}

.App {
    display: flex;
    flex-wrap: wrap;
    align-content: center;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 10PX;
    height: 100vh;
}

.logo-container {
    
    width: 20em;
    margin-bottom: 50px;
    background-color: transparent;
    flex-wrap: wrap;
    align-content: center;
    justify-content: center;
    align-items: center;
}

.logo-rh {
    width: 20em;
    margin-top: 15px;
}

.main-container {
  height: 50vh;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 50em;
}

.buttons.container {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    
}
